@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster+Two:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App{position:absolute;height:100%;text-align:center;width:100%;background-repeat:no-repeat;background-size:cover;background-position:center;-webkit-animation:25s ease-out infinite bkgimage;animation:25s ease-out infinite bkgimage}header{padding:0rem 0}header h1{color:white;font-family:"Montserrat", Verdana;margin-top:0;font-size:4rem;font-weight:bold}footer{position:fixed;bottom:0;padding:1rem 0;width:100%}footer a{text-decoration:none;color:white}footer a:hover{text-decoration:underline;color:white}.app-child{position:absolute;height:100%;text-align:center;width:100%;background:rgba(0,0,0,0.5)}@-webkit-keyframes bkgimage{0%{opacity:1;background-image:url(/portal-educativo/static/media/0.e2f08b74.jpg)}20%{opacity:1;background-image:url(/portal-educativo/static/media/1.cad8ef5b.jpg)}40%{opacity:1;background-image:url(/portal-educativo/static/media/2.f4713ca3.jpg)}60%{opacity:1;background-image:url(/portal-educativo/static/media/3.a417b7e0.jpg)}80%{opacity:1;background-image:url(/portal-educativo/static/media/4.cb69504b.jpg)}100%{background-image:url(/portal-educativo/static/media/0.e2f08b74.jpg)}}@keyframes bkgimage{0%{opacity:1;background-image:url(/portal-educativo/static/media/0.e2f08b74.jpg)}20%{opacity:1;background-image:url(/portal-educativo/static/media/1.cad8ef5b.jpg)}40%{opacity:1;background-image:url(/portal-educativo/static/media/2.f4713ca3.jpg)}60%{opacity:1;background-image:url(/portal-educativo/static/media/3.a417b7e0.jpg)}80%{opacity:1;background-image:url(/portal-educativo/static/media/4.cb69504b.jpg)}100%{background-image:url(/portal-educativo/static/media/0.e2f08b74.jpg)}}.main-container{position:relative;width:60%;padding:1rem;margin:auto;background-color:rgba(255,255,255,0.6)}.main-container form{margin-top:2rem}#pokemon{max-height:70%;font-family:"Lobster Two", "Montserrat", Verdana}#pokemon h2{font-size:3rem}#pokemon button{font-weight:bold;font-family:"Montserrat",Verdana}

